.nav {
    /* width: 100%; */
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 1rem 2rem;
    font-size: 1.2rem;
    background: rgba(255, 255, 255, 0.25);
    box-shadow: rgba(255, 255, 255, 0.25);
    width: 100vw;
    height: 70px;
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
    border: 1px solid rgba(255, 255, 255, 0.18);
    position: fixed;
    z-index: 2;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(6, auto);
    grid-gap: 10px;
    list-style: none;
    align-items: center;
    text-align: center;
    justify-content: end;
    margin: 0;
    padding: 0;
    transition: left 0.3s ease-in-out;
}

.nav-links {
    text-decoration: none;
    color: yellowgreen;
    padding: 0.7rem 1rem;
    white-space: nowrap;
    /* padding-right: 10px; */
}

.nav-links i {
    padding-right: 10px;
}

.nav-links:hover {
    background: #222;
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
}

.menu-icons {
    display: none;
    cursor: pointer;
}

.fa-bars {
    color: #222;
}
.fa-times {
    color: #f0f0f0;
}

@media screen and (max-width: 626px) {
    .nav {
        z-index: 99;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        background-color: #324452;
        /* background: rgba(255, 255, 255, .25); */
        box-shadow: 0 8px 32px 0 rgba(31, 38, 135, .37);
        backdrop-filter: blur(10px);
        border-radius: 13px;
        width: 100vw;
        height: auto;
        position: absolute;
        top: 0;
        left: -110%;
        align-items: stretch;
        padding: 80px 0 30px 0;
        /* margin: 0; */
        z-index: -1;
        /* transition: all 0.3s ease-in-out; */
    }

    .nav-menu.active {
        left: 0%;

    }

    .nav-links {
        display: block;
        widows: 100%;
        color: #f0f0f0;
    }

    .nav-links:hover {
        background-color: #f0f0f0;
        color: teal;
        border-radius: 5px;
        transition: all 0.2s ease-in-out;
    }

    .menu-icons {
        display: block;
    }

}

@media screen and (max-width: 768px) {
    .nav{
    font-size: 1rem;
    padding: 1rem 1rem;

    }
}