.contact_section {
    background-color: #edf2f8;
    padding: 2rem 1rem;
}

.section__title {
    display: flex;
    justify-content: center;
    padding: 3rem 0 2rem 0;
    font-family: serif;
    font-size: 35px;
    font-weight: 600;
    text-align: center;
}

.section__title span {
    color: hsl(271, 76%, 53%);
    margin-left: 0.5rem;
}

.contact__container {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
}

.contact__form {
    margin-left: 0; /* Reset margin for mobile view */
}

.contact__data {
    margin-top: 1rem;
    /* text-align: center; */
}

.contact__title {
    margin-bottom: 1rem;
    font-weight: 500;
}


.contact__info .info__item {
    margin-bottom: 2rem;
    display: flex;
    justify-content:center;
    align-items: center;
    column-gap: 16px;
}

.contact__info .info__icon {
    color: blueviolet;
    font-size: 22px;
}

.contact__info .info__title {
    font-size: 20px;
    font-weight: 700;
}

.info__desc {
    font-size: 18px;
    font-weight: normal;
}

.info__item.callme {
    margin-left: 140px;
}


.contact__socials {
    display: flex;
    justify-content: center;
    column-gap: 16px;
    font-size: 20px;
}

.contact__social-link {
    background-color: floralwhite;
    color: hsl(0, 0%, 40%);
    width: 40px;
    height: 40px;
    border-radius: 50%;
    text-align: center;
    line-height: 44px;
    transition: all 0.3s ease;
    box-shadow: 0 0 20px 10px rgba(0, 0, 0, 0.05);
}

.contact__social-link:hover {
    color: #edf2f8;
}

.facebook:hover {
    background: linear-gradient(120deg, #3b5998, #265a89);
}

.instagram:hover {
    background: linear-gradient(170deg, #833ab4, #fd1d1d, #f56040);
}

.github:hover {
    background: linear-gradient(120deg, #2c974b, #0c0d0d);
}

.linkedin:hover {
    background: linear-gradient(120deg, #0077B5, #00407F);
}

.form__input-group {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 15px;
}

.form__input-div {
    margin-bottom: 1rem;
}

.form__control {
    border: 1px solid hsl(0, 0%, 40%);
    background-color: #fff;
    width: 100%;
    padding: 12px 24px;
    border-radius: 30px;
    transition: all 0.3s ease;
}

.form__input-group>.form__input-div.subject {
    grid-column: span 2;
}

.form__control:focus {
    border-color: blueviolet;
}

.textarea {
    resize: none;
    height: 120px;
}

.button {
    display: inline-block;
    border: 1px solid blueviolet;
    padding: 15px 70px 15px 35px;
    font-size: 16px;
    border-radius: 35px;
    position: relative;
    overflow: hidden;
    margin: 1rem auto;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.50s ease-in-out;
}

.button:hover {
    color: aliceblue;
    background-color: blueviolet;
}

.button:hover::before {
    transform: translateX(0);
}

.button__icon {
    background-color: blueviolet;
    position: absolute;
    inset-block: -2px;
    right: 0;
    width: 55px;
    border-radius: 50%;
    color: whitesmoke;
    font-size: 20px;
    text-align: center;
    line-height: 60px;
}

@media (max-width:426px) {
    .info__item.callme {
        margin-left: 51px;
    }
    .info__item.callme2 {
        margin-right: 5rem;
    }
    .contact__data{
        text-align: none;
    }
}

@media (min-width: 769px) {
    .contact__container {
        flex-direction: row; /* Display items side by side on larger screens */
        align-items: flex-start;
    }

    .contact__data {
        width: 45%;
        text-align: left; /* Align text to the left */
        margin-right: 2rem;
    }

    .contact__form {
        width: 45%;
        margin-left: 2rem;
    }

    .form__input-group {
        grid-template-columns: repeat(2, 1fr); /* 2 columns for larger screens */
    }

    .button {
        margin: 1rem 0; /* Adjust margin */
        left: 0; /* Center align button */
    }
  
}

@media (max-width: 768px) {
    .contact__container {
        flex-direction: column; /* Stack items vertically on smaller screens */
    }

    .contact__data, .contact__form {
        width: 100%;
     
    }

    .form__input-group {
        grid-template-columns: -1fr; /* Stack form inputs vertically */
    }

    .button {
        width: 100%; /* Full width button */
    }
}

@media (max-width: 480px) {
    .section__title {
        font-size: 28px; /* Adjust font size for smaller screens */
        padding: 2rem 0 1.5rem 0; /* Adjust padding */
    }

    .form__control {
        padding: 10px 20px; /* Adjust padding for smaller screens */
    }

    .button {
        padding: 10px 50px 10px 25px; /* Adjust padding */
        font-size: 14px; /* Adjust font size */
    }

    .button__icon {
        width: 40px; /* Adjust width */
        line-height: 44px; /* Adjust line height */
        font-size: 18px; /* Adjust font size */
    }
}
