/*png.css */

.parentPng {
  background-color: #edf2f8;
  padding-top: 5rem;
}

.skill__container {
  display: flex;
  align-items: center;
  width: 100%;
}

.left {
  display: flex;
  justify-content: center;
  padding-left: 5rem;
  padding-bottom: 1rem;
  width: 50%;
}

.right {
  width: 50%;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center; /* Center content horizontally */
}

.head-text {
  display: flex;
  justify-content: center;
  padding-bottom: 3rem;
  font-size: 35px;
  font-weight: 600;
}

.png-item {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  text-align: center;
  margin: 1rem;
}

.app__skill-list {
  margin-right: 3rem;
}

.app__flex {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: #fff;
}

.app__flex img {
  width: 60%;
  height: 60%;
  background-color: white;
}

.app__flex img:hover {
  box-shadow: 0 0 25px #c7b4b6;
}

.paragraph__section {
  margin: 1rem;
  width: 100%;
  max-width: 600px; /* Add a max-width to limit paragraph width */
}

.right p {
  margin: 1rem;
}

@media (max-width: 768px) {
  .png-item {
    justify-content: center;
  }

  .head-text {
    padding-bottom: 0;
  }

  .skill__container {
    flex-direction: column;
    padding: 2rem;
  }

  .left,
  .right {
    width: 100%;
    padding: 1rem;
  }

  .head-text {
    font-size: 28px;
  }

  .app__skill-list {
    margin-right: 1rem;
  }

  .right {
    position: relative;
    align-items: center; /* Center content horizontally on smaller screens */
  }

  .right h1 {
    text-align: center;
  }
}
