.projectmain-sec {
    background-color: #edf2f8;
    padding: 5rem 1rem; /* Adjust padding for better responsiveness */
}

.exp_head {
    display: flex;
    justify-content: center;
    padding: 2rem;
}

.project-container {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap; /* Allow items to wrap on smaller screens */
    gap: 2rem; /* Add some gap between the project boxes */
}

.project-box {
    width: 100%;
    max-width: 370px; /* Set a maximum width for better control */
    height: 40vh;
    margin: 1rem;
    position: relative;
    display: flex;
    border-radius: 2rem;
    overflow: hidden;
    box-shadow: 0 0 1rem rgb(164, 163, 163);
}

.project-box img {
    border-radius: 2rem;
    width: 100%; /* Ensure the image covers the full width */
    height: 100%; /* Ensure the image covers the full height */
    object-fit: cover; /* Ensure the image covers the area while maintaining aspect ratio */
    transition: 0.5s ease;
}

.project-box:hover img {
    transform: scale(1.1);
}

.project-box .project-layer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(0, 0, 0, .1), #6b4cd2);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    padding: 0 2rem; 
    opacity: 0;
    transition: 0.5s ease;
}

.project-box:hover .project-layer {
    opacity: 1;
}

.project-layer .prohead {
    font-size: 1.9rem;
    font-weight: 600;
}

.project-layer .pro-para {
    font-size: 13px;
    margin-top: 10px;
}

.project-layer a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 3rem;
    height: 3rem;
    background-color: white;
    border-radius: 50%;
    margin-top: 1rem; /* Add margin for better spacing */
}

.project-layer a i {
    color: black;
    font-size: 18px;
}

@media (max-width: 1024px) {
    .exp_head {
        padding: 3rem 1rem; /* Adjust padding for smaller screens */
    }

    .project-box {
        width: calc(50% - 2rem); /* Adjust width for two items per row */
    }
}

@media (max-width: 768px) {
    .project-box {
        width: 100%; /* Full width on smaller screens */
    }

    .project-layer {
        padding: 0 1rem; /* Adjust padding for smaller screens */
    }

    .project-layer .prohead {
        font-size: 1.5rem; /* Adjust font size for smaller screens */
    }

    .project-layer .pro-para {
        font-size: 12px; /* Adjust font size for smaller screens */
    }
}

@media (max-width: 480px) {
    .exp_head {
        padding: 2rem 1rem; /* Adjust padding for very small screens */
    }

    .project-layer .prohead {
        font-size: 1.2rem; /* Adjust font size for very small screens */
    }

    .project-layer .pro-para {
        font-size: 11px; /* Adjust font size for very small screens */
    }
}
