.home_content {
    position: relative;
}

.home {
    background-color: #edf2f8;
    grid-template-columns: 4.32fr 9.68fr;
    align-items: center;
    padding-top: 7%;
    padding-left: 40px;
    height: 100vh;
    /* background-color: rgb(39, 37, 37); */
    overflow: hidden;

}

.home_img {
    height: 100%;
    object-fit: cover;
    border-radius: 30px;
    box-shadow: 0, 0, 8px hsla(0, 0%, 0%, 0.3);
    z-index: 1;
}

.home_data {
    max-width: 600px;
    margin-inline: auto;
    padding-top: 20px;
}

.home_title {
    position: relative;
    font-size: 50px;
    font-weight: 600;
    padding-left: 70px;
    line-height: 1.24;
    color: #42425C;
}

.home_title::before {
    content: '';
    position: absolute;
    left: 0;
    top: 25px;
    width: 40px;
    height: 5px;
    border-radius: 10px;
    background-color: black;
}

.grid {
    display: grid;
    gap: 30px;
}

.section {
    padding-block: 70px;
}

.home_des {
    line-height: 1.5;
    margin-block: 16px 32px;
    color: gray;
}

.color_block {
    position: absolute;
    width: 77%;
    height: 220%;
    top: -50%;
    left: -90%;
    background-color: yellowgreen;
    transform: rotate(80deg);

}

.home_title {
    font-family: Arial, sans-serif;
    opacity: 0;
    transform: translateY(50px);
    animation: slideIn 1.2s forwards;
}

@keyframes slideIn {
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@media (max-width: 320px) {
    * {
        padding: 0;
        margin: 0;
    }

    .home_content {
        padding: 0;
        margin: 0;
        box-sizing: border-box;
    }

    .home {
        grid-template-columns: 1fr;
        align-items: center;
        justify-content: center;
        padding-top: 1px;
        overflow: hidden;
    }

    .home_img {
        height: 70%;
    }

    .home_data {
        padding-top: 5px;
    }

    .home_title {
        font-size: 20px;
        padding-left: 50px;
    }

    .home_title span {
        display: flex;
    }

    .home_title::before {
        width: 30px;

    }

    .color_block {
        display: none;
    }

    .grid {
        display: grid;
        gap: 0px;
    }

    .section {
        padding-block: 0px;
    }
}


