@media only screen and (max-width: 576px) and (min-width: 321px) {
    .home {
        display: flex;
        flex-direction: column;
        padding-bottom: 20%;
        padding-left: 0px;
        height: auto;
        
    }

    .home_img {
        margin-top: 10%;
        display: flex;
        justify-content: center;
        width: 50%;
        height: auto;
    }

    .color_block {
        display: none;
    }

    .home_title {
        font-size: 24px;
        padding-left: 50px;
    }

    .home_des {
        font-size: 14px;
    }

    .home_title::before {
        width: 30px;
        top: 15px;
    }

    .home_content {
        width: 90%;
    }
}

@media only screen and (max-width: 768px) and (min-width:577px) {
    .home {
        grid-template-columns: 1fr;
        align-items: center;
        padding-left: 20px;
        height: 100%;
        overflow: hidden;
        display: flex;
        flex-direction: row;
        position: relative;
        height: 95vh;
    }

    .home_img {
        width: 45%;
        height: auto;
        border-radius: 15px;
        box-shadow: 0 0 8px hsla(0, 0%, 0%, 0.3);
        position: relative;
        z-index: 2;
    }

    .color_block {
        display: block;
        position: absolute;
        height: 250%;
        top: -50%;
        left: -125%;
        transform: rotate(165deg);
        overflow: hidden;
    }

    .home_data {
        width: 90%;
        padding-left: 20px;
        padding-top: 80px;
        margin-inline: auto;
    }

    .home_title {
        font-size: 24px;
    }

    .home_title::before {
        width: 30px;
    }
}

@media screen and (max-width: 992px) and (min-width: 769px) {
    .home {
        grid-template-columns: 1fr;
        align-items: center;
        padding-top: 60px;
        padding-left: 20px;
        height: auto;
        overflow: hidden;
        display: flex;
        flex-direction: row;
        position: relative;
        height: 100vh;
    }

    .home_content {
        width: 45%;
    }

    .home_title span {
        display: flex;
    }


    .home_img {
        width: 40%;
        /* height: 85%; */
        height: auto;
        border-radius: 15px;
        box-shadow: 0 0 8px hsla(0, 0%, 0%, 0.3);
        position: relative;
        z-index: 2;
        margin-left: 30px;
    }

    .color_block {
        position: absolute;
        height: 250%;
        top: -60%;
        left: -125%;
        transform: rotate(165deg);
        overflow: hidden;
        /* z-index: 1; */
    }

    .home_data {
        width: 90%;
        padding-left: 20px;
        padding-top: 80px;
        margin-inline: auto;
    }

    .home_title {
        font-size: 24px;
    }

    .home_title::before {
        width: 30px;
    }

}

@media screen and (max-width: 1200PX) and (min-width: 993px){
    .color_block {
        position: absolute;
        height: 250%;
        top: -60%;
        left: -100%;
        transform: rotate(170deg);
        overflow: hidden;
    }
}