 .about {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #edf2f8;
  padding: 20px;
  font-size: 12px;
  color: #333;
  max-height: 100vh;
  box-sizing: border-box;

}

.left-sec-about, .right-sec-about {
  flex: 1;
  width: 100%;
  margin: 10px 0;
  box-sizing: border-box;
}

.left-sec-about h1 {
  font-size: 2em;
  text-align: center;
}

.left-sec-about p {
  font-size: 1em;
  line-height: 1.5;
  text-align: justify;
  padding: 0 3rem;
}

.left-sec-about a {
  display: inline-block;
  text-align: center;
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  /* margin-top: 10px; */
  margin: 10px 3rem 0 3rem;
}
.left-sec-about a:hover{
  background-color: green;
}

.left-sec-about span{
  font-size: 1rem;
}

.left-sec-about span i {
  padding-left: 10px;
  font-size: 1rem;
}

.right-sec-about {
  display: flex;
  justify-content: center;
  box-sizing: border-box;
}

.right-sec-about img {
  max-width: 100%;
  height: auto;
}



@media (max-width: 768px) {
  .right-sec-about {
    display: none; 
  }
  .about{
    height: 100vh;
    padding-top: 80px;

  }

  .left-sec-about p {
    font-size: 1rem;
    line-height: 1.5;
    text-align: justify;
  }
}

/* Adjustments for small screens */
@media (max-width: 525px) {
  .left-sec-about p {
    font-size: 0.9em; /* Decrease font size slightly */
  }
}

@media (max-width: 375px) {
  .left-sec-about p {
    font-size: 0.8em; /* Decrease font size further for very small screens */
  }
}

@media (max-width: 320px) {
  .left-sec-about p {
    font-size: 0.7em; /* Decrease font size further for very small screens */
  }
}

@media (min-width: 768px) {
  .about {
      flex-direction: row;
  }

  .left-sec-about, .right-sec-about {
      width: 50%;
      margin: 0;
  }

  .left-sec-about h1 {
      font-size: 2.5em;
  }

  .left-sec-about p {
      font-size: 1.2em;
  }

}

@media (min-width: 1200px) {
  .left-sec-about h1 {
      font-size: 3em;
  }

  .left-sec-about p {
      font-size: 1.5em;
  }
}
